    methods: {
        /**
         * 获取自己Tycoon空间 某产品库产品 的comment 和相关图片视频链接
         */
        getOwnerCommentDetails: function (productToTweebaaId) {
            var that = this;
            var userId = this.$util.getUrlParam('userid');
            var url = this.$util.getBaseUrl() + 'api/v2/tweebaa-product/tycoon/OwnerCommentDetails?UserId=' + userId + '&ProductToTweebaaId=' + productToTweebaaId;
            this.$util.getAxois(url,"GET",{}).then(function (response) {
                var data = response.data;
                // if (data.ProdStoreCmt_Pictures && data.ProdStoreCmt_Pictures.length > 0) {
                //     data.picUrls = [];
                //     data.ProdStoreCmt_Pictures.map(function (item, index) {
                //         data.picUrls.push(item.DownloadUrl);
                //     });
                // }
                // data.ProdStoreOwnerCmtText = '';    // 翻译后的内容
                that.showcaseData = data;
            }).catch(function (error) {
                loading('hide');
                console.log(JSON.stringify(error));
            });
        },
        getBanner() {
            var i = Math.floor(Math.random() * 4) + 1;
            try {
                // this.banner = require('../../image/banner' + i + '-' + this.$util.language + '.jpg');
                this.banner = require('../../image/banner' + 1 + '-' + this.$util.language + '.jpg');
            } catch (e) {

            }
        },
        getCountryCode() {
            var that = this;
            // $.getJSON("http://ip-api.com/json/?callback=?", function (data) {
            //     that.countryCode = data.countryCode;
            // });
            axios.get("https://api.ipdata.co/?api-key=bd616afaef799bfe15627291f24ad3fa83c897a6a1219caa8c42928b") // 使用eric.guo@leivaire.com 申请的code
                .then(function (response) {
                    console.log(response);
                    that.countryCode = response.data.country_code;
                })
                .catch(function (error) {
                    console.log(error);
                });
        },
        _pars(obj) {
            let results = [];
            for (let key in obj) {
                results.push(key + '=' + obj[key]);
            }
            return results.join('&');
        },
        getProductDetail() {
            var that = this;
            var userId = this.$util.getUrlParam('userid');
            var prodid = this.$util.getUrlParam('prodid');
            var url = this.$util.getBaseUrl() + "api/users/" + userId + "/products/" + prodid + '?ProductToTweebaaDetailRequired=true&needProductType=true&LanguageCode=' + this.$util.language;
            this.$util.getAxois(url, "GET", {}).then(function (response) {
                // var div = document.getElementById("code");
                // div.dataset.clipboardText = that.shareid;
                var productData = response.data;
                that.productData = productData;

                if (productData.ProductType === 1 && productData.LatestProductToTweebaa) {
                    that.getOwnerCommentDetails(productData.LatestProductToTweebaa.ProductToTweebaaId);
                }

                var loading = document.getElementById('loadingText');
                var parentObj = loading.parentNode;
                parentObj.removeChild(loading);

                setTimeout(function () {
                    initialize();
                    that.getAllPorducts(response.data.Owner.Id);
                    var url = that.$util.getBaseUrl() + "/api/data-analytics/save-view-data";
                    var pram = {
                        TypeId: 2,
                        ProductId: prodid,
                        UserId: userId,
                        CountryCode: that.countryCode,
                        StayTimeSeconds: Math.floor(Math.random() * (120 - 10)) + 10
                    };
                    //console.log(JSON.stringify(pram));
                    that.$util.getAxois(url, "POST", pram).then(function (response) {
                        //console.log(JSON.stringify(response.data));
                    }).catch(function (error) {
                        console.log(JSON.stringify(error) + '#######################');
                    });
                    that.paramsForApp = that._pars({
                        prodid: prodid,
                        userinfo: JSON.stringify(that.productData.Owner),
                        countrycode: that.countryCode,
                        page: that.$util.getUrlParam('page')
                    });
                });
            }).catch(function (error) {
                console.log(error);
            });
        },
        getAllPorducts(ownerId) {
            var that = this;
            var url = this.$util.getBaseUrl() + "/api/v2/users/products";
            var paramStr = {
                "ownerId": ownerId,
                "currentpage": 0,
                "itemsperpage": 3
            };
            this.$util.getAxois(url, "POST", paramStr).then(function (response) {
                //console.log(JSON.stringify(response));
                that.topProductData = response.data.Data;

            }).catch(function (error) {
                console.log(JSON.stringify(error));
                //messages($.i18n.prop('systemError'), 'error', 'top', 3);
            });
        },
        switchImgVideo(type) {
            this.switchImg = type === 'img';
            initialize();
        }
    }
